@import "core/base";

/*start*/
.jigsaw-time-host {
    color: $text-color;
    .bootstrap-datetimepicker-widget table td.old, .bootstrap-datetimepicker-widget table td.new {
        color: $text-color-secondary;
    }

    .bootstrap-datetimepicker-widget table td.day {
        height: 14px;
        line-height: 14px;
    }

    .bootstrap-datetimepicker-widget .datepicker-decades tbody td span {
        display: none;
        &.decade {
            display: inline-block;
            line-height: 24px !important;
            width: 72px;
        }
    }

    .bootstrap-datetimepicker-widget table td span.active,
    .bootstrap-datetimepicker-widget table td.active,
    .bootstrap-datetimepicker-widget table td.active:hover,
    .jigsaw-time-box .bootstrap-datetimepicker-widget table tr.active td {
        background-color: $primary-color;
        color: $text-color-of-bg;
    }

    .bootstrap-datetimepicker-widget table th.picker-switch {
        font-weight: 700 !important;
    }
    .bootstrap-datetimepicker-widget table thead tr:first-child th {
        &:hover {
            background: none;
            color: $primary-color;
        }
        span.glyphicon {
            @include font-awesome();
            font-size: 16px;
            &.glyphicon-chevron-left:before {
                content: "\f104";
            }
            &.glyphicon-chevron-right:before {
                content: "\f105";
            }
        }

    }

    .bootstrap-datetimepicker-widget .picker-switch a[data-action] {
        color: $primary-color;
    }
    .bootstrap-datetimepicker-widget a[data-action] {
        color: $text-color;
        &:hover {
            color: $primary-color;
        }
    }

    .bootstrap-datetimepicker-widget .picker-switch td span {
        height: 24px;
        line-height: 24px;
        &.glyphicon {
            @include font-awesome();
            font-size: 14px;
            line-height: 24px;
            &.glyphicon-time:before {
                content: "\f017";
            }
            &.glyphicon-calendar:before {
                content: "\f073";
            }
        }
    }

    .bootstrap-datetimepicker-widget table td span {
        height: 24px;
        line-height: 24px;
    }

    .bootstrap-datetimepicker-widget table td a.btn span {
        &.glyphicon {
            @include font-awesome();
            font-size: 16px;
            line-height: 24px;
            &.glyphicon-chevron-up:before {
                content: "\f106";
            }
            &.glyphicon-chevron-down:before {
                content: "\f107";
            }
        }
    }

    .bootstrap-datetimepicker-widget .timepicker-hour,
    .bootstrap-datetimepicker-widget .timepicker-minute,
    .bootstrap-datetimepicker-widget .timepicker-second {
        font-weight: 400;
    }

    @include ie11Hack() {
        .bootstrap-datetimepicker-widget table td.active,
        .bootstrap-datetimepicker-widget table td.active:hover {
            text-shadow: none;
        }
        .bootstrap-datetimepicker-widget table td span.active {
            text-shadow: none;
        }
    }

    .bootstrap-datetimepicker-widget .timepicker table td {
        height: 68px;
        line-height: 68px;
    }

    .bootstrap-datetimepicker-widget.usetwentyfour .timepicker table td.hour{
        height: 34px;
        line-height: 34px;
    }
}

/*end*/

/*zTreestart*/
